// =============================================================================
// POST /api/pay - SMF Payment Endpoint
// =============================================================================
// Creates a charge via the SMF Drop-In Facilitator SDK
// Requires authentication and validates all inputs

import { NextRequest, NextResponse } from 'next/server';
import { z } from 'zod';
import { requireAuth } from '@/lib/auth';
import { validateRequestBody, createValidationErrorResponse } from '@/lib/validate-request';
import { extractRequestContext, wrapResponse, createErrorResponse } from '@/lib/request-logging';
import { rateLimitByApiKey, addRateLimitHeaders, createRateLimitResponse } from '@/lib/rate-limit-unified';
import { 
  withSmfCorrelation, 
  classifySmfError, 
  getUserFriendlyErrorMessage,
  SmfError,
  isSmfConfigured,
} from '@/payments/smfClient';
import { createCharge, getChargeByOrderId, getChargeByIdempotencyKey, updateChargeStatus } from '@/db/smf-payments';
import { createLogger } from '@/lib/logger';

const logger = createLogger({ component: 'PayAPI' });

// -----------------------------------------------------------------------------
// Request Schema
// -----------------------------------------------------------------------------

const paymentMethodSchema = z.object({
  type: z.enum(['card', 'bank_account', 'wallet', 'crypto']),
  token: z.string().min(1, 'Payment method token is required'),
});

const chargeRequestSchema = z.object({
  // Required fields
  merchantId: z.string().min(1, 'merchantId is required'),
  amount: z.number().int().positive('Amount must be a positive integer (minor units)'),
  currency: z.string().length(3, 'Currency must be a 3-letter ISO code').default('USD'),
  paymentMethod: paymentMethodSchema,
  
  // Optional fields
  platformId: z.string().optional(),
  orderId: z.string().optional(),
  idempotencyKey: z.string().optional(),
  metadata: z.record(z.unknown()).optional(),
});

type ChargeRequest = z.infer<typeof chargeRequestSchema>;

// -----------------------------------------------------------------------------
// POST Handler
// -----------------------------------------------------------------------------

export async function POST(request: NextRequest) {
  const ctx = await extractRequestContext(request, '/api/pay');

  try {
    // Check if SMF is configured
    if (!isSmfConfigured()) {
      return createErrorResponse(
        ctx,
        'Payment service is not configured. Please set SMF_API_KEY.',
        503,
        'SERVICE_UNAVAILABLE'
      );
    }

    // Rate limiting
    const rateLimitResult = await rateLimitByApiKey(request);
    if (rateLimitResult && !rateLimitResult.allowed) {
      logger.warn({ apiKeyId: ctx.apiKeyId, limit: rateLimitResult.limit }, 'Rate limit exceeded');
      return wrapResponse(createRateLimitResponse(rateLimitResult), ctx);
    }

    // Authentication
    const authResult = await requireAuth(request, 'user');
    if ('response' in authResult) {
      return wrapResponse(authResult.response, ctx);
    }

    const { apiKey } = authResult;
    ctx.apiKeyId = apiKey.id;

    // Validate request body
    const validation = await validateRequestBody(request, chargeRequestSchema);
    if (!validation.valid) {
      return createValidationErrorResponse(validation);
    }

    const {
      merchantId,
      amount,
      currency,
      paymentMethod,
      platformId,
      orderId,
      idempotencyKey,
      metadata,
    } = validation.data;

    // Generate idempotency key if not provided (using orderId if available)
    const effectiveIdempotencyKey = idempotencyKey || (orderId ? `order_${orderId}` : undefined);

    // Check for existing charge with same idempotency key or order ID
    let existingCharge = null;
    
    // First check by idempotency key (more reliable)
    if (effectiveIdempotencyKey) {
      existingCharge = await getChargeByIdempotencyKey(effectiveIdempotencyKey);
    }
    
    // Fall back to order ID check
    if (!existingCharge && orderId) {
      existingCharge = await getChargeByOrderId(orderId);
    }

    // Return cached result if charge already exists and succeeded
    if (existingCharge && existingCharge.status === 'succeeded') {
      logger.info(
        { 
          orderId, 
          idempotencyKey: effectiveIdempotencyKey,
          chargeId: existingCharge.id,
        },
        'Returning existing successful charge (idempotent)'
      );
      return NextResponse.json({
        success: true,
        charge: {
          id: existingCharge.smf_charge_id,
          status: existingCharge.status,
          amount: existingCharge.amount,
          currency: existingCharge.currency,
          orderId: existingCharge.order_id,
          createdAt: existingCharge.created_at,
        },
        cached: true,
      });
    }
    
    // If charge exists but is pending/requires_action, also return it
    if (existingCharge && ['pending', 'requires_action'].includes(existingCharge.status)) {
      logger.info(
        { 
          orderId, 
          idempotencyKey: effectiveIdempotencyKey,
          chargeId: existingCharge.id,
          status: existingCharge.status,
        },
        'Returning existing in-progress charge (idempotent)'
      );
      return NextResponse.json({
        success: true,
        charge: {
          id: existingCharge.smf_charge_id,
          status: existingCharge.status,
          amount: existingCharge.amount,
          currency: existingCharge.currency,
          orderId: existingCharge.order_id,
          createdAt: existingCharge.created_at,
        },
        cached: true,
      });
    }

    // Resolve platform ID (use from request or default from API key context)
    const effectivePlatformId = platformId || apiKey.platformId || 'plt_default';

    logger.info(
      {
        merchantId,
        amount,
        currency,
        orderId,
        platformId: effectivePlatformId,
        hasIdempotencyKey: !!effectiveIdempotencyKey,
      },
      'Creating SMF charge'
    );

    // Create charge via SMF SDK with thread-safe correlation tracking
    const charge = await withSmfCorrelation(ctx.requestId, (smf) =>
      smf.charge({
        platformId: effectivePlatformId,
        merchantId,
        amount,
        currency,
        paymentMethod: {
          type: paymentMethod.type,
          token: paymentMethod.token,
        },
        idempotencyKey: effectiveIdempotencyKey,
        metadata: {
          order_id: orderId,
          user_id: apiKey.userId,
          api_key_id: apiKey.id,
          ...metadata,
        },
      })
    );

    // Store charge in database with error recovery
    try {
      await createCharge({
        smf_charge_id: charge.id,
        platform_id: effectivePlatformId,
        merchant_id: merchantId,
        amount,
        currency,
        status: charge.status as any,
        order_id: orderId,
        user_id: apiKey.userId,
        idempotency_key: effectiveIdempotencyKey,
        fees: charge.fees,
        payment_method_type: paymentMethod.type,
        metadata: {
          order_id: orderId,
          ...metadata,
        },
        correlation_id: ctx.requestId,
      });
    } catch (dbError) {
      // CRITICAL: SMF charge succeeded but DB write failed
      // Log with high severity for reconciliation
      logger.error(
        {
          smfChargeId: charge.id,
          orderId,
          merchantId,
          amount,
          currency,
          status: charge.status,
          idempotencyKey: effectiveIdempotencyKey,
          correlationId: ctx.requestId,
          dbError: dbError instanceof Error ? dbError.message : 'Unknown DB error',
        },
        'ORPHANED_CHARGE: SMF charge created but DB record failed - requires reconciliation'
      );
      // Continue to return success to client (charge was created)
      // The charge can be reconciled via webhook or manual process
    }

    logger.info(
      {
        chargeId: charge.id,
        status: charge.status,
        orderId,
      },
      'SMF charge created successfully'
    );

    // Build response
    const response = NextResponse.json({
      success: true,
      charge: {
        id: charge.id,
        status: charge.status,
        amount: charge.amount,
        currency: charge.currency,
        fees: charge.fees,
        orderId,
        createdAt: charge.createdAt,
        // Include next action if required (e.g., 3DS)
        ...(charge.nextAction && { nextAction: charge.nextAction }),
      },
    });

    if (rateLimitResult) {
      addRateLimitHeaders(response, rateLimitResult);
    }

    return wrapResponse(response, ctx);

  } catch (error) {
    // Handle SMF errors with proper classification
    if (error instanceof SmfError) {
      const classified = classifySmfError(error);
      
      logger.warn(
        {
          code: classified.code,
          type: classified.type,
          message: classified.message,
          retryAfter: classified.retryAfterSeconds,
        },
        'SMF charge failed'
      );

      // Build error response based on error type
      const errorResponse: Record<string, unknown> = {
        success: false,
        error: {
          code: classified.code,
          message: getUserFriendlyErrorMessage(classified.code),
          type: classified.type,
        },
        requestId: ctx.requestId,
      };

      // Include retry hint for temporary errors
      if (classified.type === 'temporary' && classified.retryAfterSeconds) {
        errorResponse.retryAfter = classified.retryAfterSeconds;
      }

      // Include field info for validation errors
      if (classified.code === 'validation_error' && (error as any).field) {
        (errorResponse.error as Record<string, unknown>).field = (error as any).field;
      }

      const response = NextResponse.json(errorResponse, { status: classified.httpStatus });
      return wrapResponse(response, ctx);
    }

    // Unknown error
    return createErrorResponse(
      ctx,
      error instanceof Error ? error : new Error('Failed to create charge'),
      500,
      'INTERNAL_ERROR'
    );
  }
}

